.friendMessage {

}

.friendImageAndText {
    display: flex;
    margin-left: 70px;
}

.friendImageAndText img {
    width: 48px;
    height: 48px;
    top: 204px;
    left: 1162px;
}

.friendText {
    background-color: rgba(245, 247, 251, 1);
    color: rgba(0, 0, 0, 1);
    max-width: 414px;

    text-align: center;
    border-radius: 10px;
    padding: 8px 15px;
    position: relative;
    margin: 0 0 0 10px;
}

.friendText:after {
    content: '';
    bottom: 0;
    position: absolute;
    left: -10px;
    width: 0px;
    height: 0px;
    border: 20px solid transparent;
    border-bottom-color: rgba(245, 247, 251, 1);
}

.friendName {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 600;
    line-height: 19.5px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;

}

.friendMessageText {
    font-family: Montserrat;
    font-size: 16px;
    font-weight: 400;
    line-height: 19.5px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;

}

.friendTime {
    margin-left: 80px;
    font-family: Montserrat;
    font-size: 10px;
    font-weight: 600;
    line-height: 12.19px;
    text-align: left;
    text-underline-position: from-font;
    text-decoration-skip-ink: none;

}
